<script>
// const axios = require("axios");
import axios from "../../axios/api";
export default {
  data() {
    return {
      c_jwt: this.jwt,
      notices_list: [],
    };
  },
  async mounted() {
    //document.getElementsByClassName("vue-loaders")[0].hidden = false;
    const axiosConfig = {
      method: "get",
      url: "https://www.tkbgo.com.tw/api/notice/getNoticeList",
      headers: { "x-access-token": this.jwt },
    };
    try {
      const response = await axios(axiosConfig, this);
      const json = await response.data;
      //const { data } = json;
      console.log(json);
      this.news_list = this.news_list.concat(this.news_list, json);
      //document.getElementsByClassName("vue-loaders")[0].hidden = true;
    } catch (err) {
      console.log(err);
    }
  },
  computed: {
    news_list_c() {
      let list = this.news_list;
      list.forEach((i) => {
        i.content_c = i.content.replace(/<[^>]+>/g, "");
      });
      return list;
    },
  },
};
</script>

